import handleActions from '../utils/handleActions'
import { SET_USER, SetUserAction, UNSET_USER, SET_USER_LOADING } from '../actions/user'

export interface IUserState {
	id: string
	email: string
	token: string
	loggedIn: boolean
	loading: boolean
}

const persistentState: Partial<IUserState> = localStorage.userState && JSON.parse(localStorage.userState)

const emptyState: IUserState = {
	id: '',
	email: '',
	token: '',
	loggedIn: false,
	loading: false,
}
const initialState: IUserState = {
	...emptyState,
	...persistentState,
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_USER](state, { payload }: SetUserAction) {
			const newState: IUserState = {
				...state,
				id: payload.id,
				email: payload.id,
				token: payload.token,
				loggedIn: true,
			}
			localStorage.userState = JSON.stringify(newState)
			return {
				...newState,
				loading: false,
			}
		},
		[UNSET_USER]() {
			localStorage.removeItem('userState')
			return emptyState
		},
		[SET_USER_LOADING](state) {
			return {
				...state,
				loading: true,
			}
		},
	},
	initialState,
)



